import { v4 as uuidv4 } from 'uuid';

export default class Tile 
{

    _uuid;
    get uuid () {
        return this._uuid
    }

    _sides = [
        // up ,
        // right,
        // down,
        // left
    ];

    get sides () {
        return this._sides.slice()
    }

    _asset = '';

    get asset() {
        return this._asset;
    }

    _rotation = 0;

    get rotation () {
        return this._rotation;
    }
    set rotation (val) {
        this._rotation = val % 4;
    }

    constructor(up, right, down, left, asset, rotation = 0, uuid = null) {
        this._sides = [up, right, down, left];
        this._rotation = rotation;
        this._asset = asset

        this._uuid = uuid || uuidv4()
    }

    clone() {
        return new Tile(...this.sides, this.asset, this.rotation, this._uuid);
    }

    // rotate the tile by one quarter turn
    rotate(nbRot = 1) 
    {
        this._rotation = (this._rotation + nbRot) % 4;
        return this;
    }

    // reset rotation to 0
    resetRotation() 
    {
        this._rotation = 0;
        return this;
    }

    getUp() {
        return this._sides.slice(this.rotation % 4, this.rotation % 4 + 1)[0]
    }

    getRight() {
        return this._sides.slice((this.rotation + 1) % 4, (this.rotation + 1) % 4 + 1)[0]
    }

    getDown() {
        return this._sides.slice((this.rotation + 2) % 4, (this.rotation + 2) % 4 + 1)[0]
    }

    getLeft() {
        return this._sides.slice((this.rotation + 3) % 4, (this.rotation + 3) % 4 + 1)[0]
    }
}