<template>
    <div 
        class="game"
    >
        <div
            v-if="!this.game.isStarted"
            class="game__logo"
        >
            <img src="../assets/logo.png" alt="">
        </div>
        <div
            v-if="game.isStarted"
            class="game__inner"
        >
            <board 
                :grid="gridData" 
                :nb-col="game.board.nbCol" 
                :nb-row="game.board.nbRow" 
                :active-uuid="activeUuid"

                :onClickSlot="clickSlot"
            />
            <stack 
                :uptile="game.nextTile"
                :nextTileInGame="nextInGame"
            />
        </div>
        <div
            :class="[ 'game__btnPanel', { 'game__btnPanel--fixed-bottom': this.game.isStarted } ]"
        >
            <input 
                @click="startGame()"
                type="button"
                value="start a new game" />

            <input 
                v-if="nextInGame"
                @click="validateMove()"
                type="button"
                value="validate move" />

        </div>        
    </div>
</template>

<script>
    import board from './board';
    import stack from './stack'

    export default {
        props:[ 
            'game' 
        ],
        components: {
            board,
            stack
        },
        computed: {
            gridData() {
                return this.game.board.grid.map((c, i) => {
                    if (!c) {
                        if (this.game.board.isPlayable(this.game.nextTile, i)) {
                            return { enabled: true }
                        }    
                    }
                    return c;
                })
            },
            isStarted() {
                return this.game.isStarted
            },
            nextInGame() {
                return this.game.nextTileInGame;
            },
            activeUuid() {
                return this.game.nextTile ? this.game.nextTile.uuid : ""
            }
        },
        methods: {
            startGame() {
                this.game.start()
            },
            clickSlot(position) {
                const board = this.game.board;
                const positionIsEmpty = board.grid[position] === null
                const positionIsInGameTile = !positionIsEmpty && board.grid[position].uuid === this.game.nextTile.uuid

                const nextTile = this.game.nextTile.clone();

                if (positionIsEmpty && board.isPlayable(nextTile, position)
                    || positionIsInGameTile
                ) {    
                    let rotation = positionIsInGameTile ? (nextTile.rotation + 1) : 0;
                    nextTile.rotation = rotation
                    while (!board.testMove(nextTile, position)) {
                        nextTile.rotate();
                        rotation++;
                    }
                    this.game.playMove(position, rotation)
                }
            },
            validateMove() {
                this.game.commitMove()
            }
        }
    }
</script>

<style lang="sass" scoped>
    .game
        position: relative
        width: 100%
        height: 100%

        &__logo
            position: absolute
            top: 200px
            left: 50%
            transform: translate(-50%)

        &__inner
            width: 100%
            height: 100%

        &__btnPanel
            position: absolute
            top: 400px
            left: 50%
            transform: translate(-50%)

            &--fixed-bottom
                position: fixed
                top: auto
                bottom: 0
                left: 0
                transform: none

</style>