import tiles from '../ tiles'
import Board from './Board';
import Stack from './Stack';
import Tile from './Tile';
import { starterTile } from '../ tiles';

export default class Game
{

    _board;
    get board () {
        return this._board;
    }

    _stack;
    get stack () {
        return this._stack
    }

    _nextTile;
    get nextTile () {
        return this._nextTile;
    }

    _isStarted = false
    get isStarted () {
        return this._isStarted;
    }

    _nextTileInGame = false
    get nextTileInGame () {
        return this._nextTileInGame;
    }

    _players = []
    _turn = 0 // index that indicates whose player is playing

    constructor(nbPlayer = 2)
    {
        for(let i = nbPlayer; i; i--) {
            this._players.push({
                score: 0
            })
        }
    }

    start() {

        this._stack = new Stack(tiles)
        this._board = new Board()

        this._stack.shuffle();

        this._board.putTile(new Tile(...starterTile.sides, starterTile.asset))

        this._drawTile()

        this._nextTileInGame = false
        this._isStarted = true
    }

    playMove(position, rotation) 
    {
        this.board.clearPosition(this.board.findPosition(this.nextTile.uuid));
        this.nextTile.rotation = rotation;
        this.board.putTile(this.nextTile, position);

        this._nextTileInGame = true
    }

    commitMove() {
        this._drawTile()
    }

    _drawTile() {
        this._nextTile = this._stack.pop()
        this._nextTileInGame = false
    }

}