
export default class Board
{

    _nbCol = 1;

    _grid = [] ;


    get grid () {
        return this._grid
    }

    get nbCol () {
        return this._nbCol
    }

    get nbRow () {
        return this._grid.length / this._nbCol
    }

    putTile(tile, position = 0)
    {
        if (this._grid.length === 0 || this.testMove(tile, position)) {

            this._grid = [...this._grid.slice(0, position), tile, ...this._grid.slice(position + 1)]
        }

        this._updateBoardSize()
    }

    isPlayable(tile, position) {


        const t = tile;
        const tileRotations = [
            t.clone()
            , t.clone().rotate(1)
            , t.clone().rotate(2)
            , t.clone().rotate(3)
        ]

        return tileRotations.reduce((p, rotatedTile) => {
            return p || this.testMove(rotatedTile, position)
        }, false)
    }

    testMove(tile, position) 
    {
        const isPendingTile = (boardTile, newTile) => { 
            return boardTile && newTile.uuid === boardTile.uuid 
        }
        const nextTiles = [
            (!isPendingTile(this._grid[position - this._nbCol], tile) ? this._grid[position - this._nbCol] : null)
            , (!isPendingTile(this._grid[position + 1], tile) ? this._grid[position + 1] : null)
            , (!isPendingTile(this._grid[position + this._nbCol], tile) ? this._grid[position + this._nbCol] : null)
            , (!isPendingTile(this._grid[position - 1], tile) ? this._grid[position - 1] : null)
        ].map((t, i) => t ? i : null).filter(c => c !== null);

        const constraints = [
            () => this._grid[position - this._nbCol].getDown() === tile.getUp(), 
            () => this._grid[position + 1].getLeft() === tile.getRight(), 
            () => this._grid[position + this._nbCol].getUp() === tile.getDown(), 
            () => this._grid[position - 1].getRight() === tile.getLeft(), 
        ];

        if (nextTiles.length) {
            return constraints.filter((c, i) => nextTiles.indexOf(i) !== -1).reduce((cumul, test) => {
                return (
                    cumul !== false
                    && !!test()
                )
            }, null)    
        }
        return false;
    }

    findPosition(uuid) {
        return this._grid.findIndex(t => t && t.uuid === uuid)
    }

    clearPosition(position) {
        this._grid[position] = null
    }

    _updateBoardSize () {
        const hasTileInFirstRow = this._grid.slice(0, (this._nbCol > 1 ? this._nbCol - 1 : undefined)).reduce((p, c) => (p || c !== null), false)
        const hasTileInLastRow  = this._grid.slice(this._grid.length - this._nbCol).reduce((p, c) => (p || c !== null), false)

        const hasTileInFirstCol = this._grid.filter((c, i) => !(i % this._nbCol)).reduce((p, c) => (p || c !== null), false)
        const hasTileInLastCol  = this._grid.filter((c, i) => !((i + 1) % this._nbCol)).reduce((p, c) => (p || c !== null), false)

        if (hasTileInFirstCol) {
            const rows = []

            for(let i = 0; i < this.nbRow; i++) rows.push(this._grid.slice(i * this.nbCol, i * this.nbCol + this.nbCol));

            this._grid = rows.map(r => [ null, ...r ]).reduce((c, r) => c.concat(...r), [])

            this._nbCol += 1
        }

        if (hasTileInLastCol) {
            const rows = []
            
            for(let i = 0; i < this.nbRow; i++) rows.push(this._grid.slice(i * this.nbCol, i * this.nbCol + this.nbCol));
            
            this._grid = rows.map(r => [ ...r, null ]).reduce((c, r) => c.concat(...r), [])

            this._nbCol += 1
        }

        if (hasTileInFirstRow) {
            const newRow = []

            for(let i = this._nbCol; i; i--) newRow.push(null);

            this._grid = [...newRow, ...this._grid]
        }

        if (hasTileInLastRow) {
            const newRow = []

            for(let i = this._nbCol; i; i--) newRow.push(null);

            this._grid = [...this._grid, ...newRow]
        }
    }

}


