import Tile from './Tile'

export default class Stack
{

    _tiles = []

    constructor(tiles) {
        this._tiles = tiles.map((t) => new Tile(...t.sides, t.asset))
    }

    shuffle() {
        this._tiles = this._tiles.sort(() => (Math.random() - 0.5))
    }

    pop() {
        return this._tiles.pop()
    }
}