<template>
    <div
        class="tile"
        :style="tileStyle"
    >
        <div
        >
            <span 
                v-for="s in data.sides.length"
                :key="s"
                class="tile__sides"
            >
                {{ data.sides[s - 1] }}
            </span>
        </div>
    </div>    
</template>

<script>
export default {
    props: [
        'data',
        'active'
    ],
    computed: {
        tileStyle() {
            const styles = { 
                'backgroundImage': 'url(' + this.data.asset + ')', 
                'transform': 'rotate(' + -this.data.rotation * 90 + 'deg)' 
            }

            if (this.active) {
                styles.transitionDuration = '0.2s'
            }

            return styles;
        }
    }
}
</script>

<style lang="sass" scoped>
.tile
    position: relative
    height: 100%
    border: 1px solid #000

    background-size: cover

    transition: transform 0

    &__sides
        position: absolute

        &:nth-of-type(1)
            top: 0
            left: 50%

        &:nth-of-type(2)
            right: 0
            top: 50%

        &:nth-of-type(3)
            bottom: 0
            left: 50%

        &:nth-of-type(4)
            left: 0
            top: 50%    
</style>