<template>
    <div class="board">
        <ul
            :style="{ gridTemplate: gridDef }"
        >
            <li 
                v-for="i in gridData.length"
                :key="i"
            >
                <div
                    :class="['board__slot', { 'board__slot--enabled': gridData[i - 1].enabled }]"
                    @click="clickSlot(i-1)"
                >
                    <tile 
                        v-if="gridData[i - 1].sides" 
                        :data="gridData[i - 1]" 
                        :active="gridData[i - 1].uuid === activeUuid"
                    />
                </div>
            </li>
        </ul>
    </div>
</template>

<script>
import tile from './tile'

export default {
    components: {
        tile
    },
    props: [
        'grid',
        'nbCol',
        'nbRow',
        'activeUuid',
        'onClickSlot'
    ],
    computed: { 
        gridData() {
            return this.grid.map( c => c === null ? { empty: true } : c )
        },
        gridDef () {
            let template = ""
            for(let i = this.nbRow; i; i--) template += " 100px";
            template += ' /' 
            for(let i = this.nbCol; i; i--) template += " 100px";
            return template;
        }
    },
    methods: {
        clickSlot(position) {
            this.onClickSlot && this.onClickSlot(position)
        }
    }
}
</script>

<style lang="sass" scoped>
    ul, li
        margin: 0
        padding: 0

    li
        list-style: none

    .board
        height: 100%
        background: url('../assets/background.jpeg')
        background-repeat: repeat
        background-size: auto 100%
        position: relative

    ul 
        display: grid
        position: absolute

    li
        display: block
        height: 100px
        width: 100px


    .board
        &__slot
            height: 100%

            &--enabled
                border: 1px solid #EEE

</style>